.slider_component{
    height: 90%;
    width: 30%;
    border: 1px solid wheat;
    display: flex;
    text-align: left;
    position: relative;
    
}
.slider_component:hover{
    cursor: pointer;
    box-shadow: 0 0 0 9px rgba(0, 0, 0, 0.8);
    transform: translate3d(0, 0, 0);
    position: relative;
    display: block;
    box-sizing: border-box;
    touch-action: pan-y;
    transition-duration: 1s;
    transform: scale(1.1, 1.08);
}


.slider_comp_text{
    font-size: 50px;
    font-weight: 800;
    font-family: "TT Hoves", "Arial", sans-serif;
    color: rgb(221, 221, 221);
    width: 70%;
    background-color: rgba(0, 0, 0, 0.55);
    overflow:hidden;
    
}

.slider_comp_text:hover{
    box-shadow: 0 0 0 9px rgba(0, 0, 0, 0.8);
}